.form {
	position: relative;
	z-index: 1;
	background: #24242d;
	width: 100%;
	padding: 5%;
	text-align: center;
	margin: 30% auto 0 auto;
}

.form input {
	outline: 0;
	background: #d3d3d3;
	width: 100%;
	border: 0;
	margin: 0 0 15px;
	height: 60px;
	padding: 10px;
	box-sizing: border-box;
	font-size: 20px;
	font-weight: 600;
	color: black;
	border-radius: 8px;
}

.form input::placeholder {
	color: #555555;
	font-weight: 400;
}

.form button {
	position: relative;
	overflow: hidden;
	background: #2f4f4f;
	width: 100%;
	height: 60px;
	padding: 15px;
	color: #d3d3d3;
	font-size: 20px;
	font-weight: 500;
	cursor: pointer;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.form .message {
	margin: 15px 0 0;
	color: #d3d3d3;
	font-size: 18px;
}

.form .message a {
	color: #32cd32;
	text-decoration: none;
}

/* Исходная анимация переключения */
.hidden {
	display: none;
}

.message a {
	cursor: pointer;
}
