.Filters {
	display: flex;
	flex-direction: column;
	position: absolute;
	height: 230px;
	width: 100%;
	top: 60px;
	left: 0;
	z-index: 4;
}

.Filters .main {
	flex-direction: column;
	display: flex;
	height: 100%;
	width: 100%;
	padding: 10px 0;
	gap: 0;
	background-color: #24242d;
	border-radius: 0 0 30px 30px;
	z-index: 3;
}

.Filters .main .group {
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: relative;
	height: 25%;
	padding: 0 30px;
	overflow: hidden;
	z-index: 4;
}
.Filters .main .group p {
	font-size: 20px;
	font-weight: 200;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Filters .main .group .options {
	display: flex;
	align-items: center;
	height: 100%;
	width: 90px;
	justify-content: space-between;
}
.Filters .main::before {
	position: absolute;
	content: '';
	background-color: #24242d;
	width: 100%;
	height: 30px;
	top: -30px;
	border: solid 0;
	border-bottom-width: 1px;
	border-image: linear-gradient(to right, #24242d, #555555, #24242d) 1;
	z-index: 3;
}

.Filters .pseudo {
	z-index: 2;
}

.Filters .pseudo::before {
	position: absolute;
	content: '';
	background-color: #171722;
	width: 100%;
	height: 50px;
	bottom: -20px;
}

.Filters .pseudo::after {
	position: absolute;
	content: '';
	top: 0;
	left: -30px;
	width: 100vw;
	height: 100vh;
	background-color: rgba(23, 23, 34, 0.9);
}
