.Home header {
	display: flex;
	justify-content: space-between;
	gap: 10px;
}
.Home header button {
	position: relative;
	overflow: hidden;
	color: #d3d3d3;
	font-size: 16px;
	cursor: pointer;
}
.Home header button:nth-child(1) {
	background-color: #2f4f4f;
	padding: 10px 0;
	width: 140vw;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Home header button:nth-child(2) {
	border: solid #2f4f4f 1px;
	padding: 10px 0;
	width: 100%;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.Home main .title {
	margin: 20px 6%;
}
.Home main .title h1 {
	font-size: 40px;
	font-weight: 500;
}
.Home main .title h1 span {
	font-weight: 800;
}
.Home main .title h4 {
	line-height: 1.3em;
	font-size: 20px;
	margin-top: 20px;
	font-weight: 400;
}
.Home main .title h4 a {
	color: #32cd32;
}
.Home main .title h4 span {
	font-weight: 500;
}
.Home main button {
	position: relative;
	overflow: hidden;
	margin-top: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 24px;
	font-weight: 500;
	color: #d3d3d3;
	background-color: #2f4f4f;
	height: 100px;
	width: 100%;
	cursor: pointer;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Home main button svg {
	margin-left: 8px;
	width: 25px;
	aspect-ratio: 1/1;
	color: #d3d3d3;
}
