.Search header {
	background-color: #171722;
	position: relative;
	box-sizing: border-box;
	width: 100%;
	top: 0;
	left: 0;
}
.Search header .searchPanel {
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: absolute;
	height: 60px;
	width: 100%;
	background-color: #24242d;
	border-radius: 50px;
	padding: 0 5%;
}
.Search header .searchPanel #searchArea {
	width: 85%;
	height: 100%;
	margin-left: 2%;
	z-index: 5;
	color: #d3d3d3;
	font-size: 20px;
	font-weight: 400;
}
.Search header .searchPanel #searchArea::placeholder {
	color: #555555;
	font-size: 20px;
	font-weight: 400;
}

.Search main {
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow: hidden;
	padding-top: 80px;
	z-index: 1;
}
.Search main ul.one-column {
	list-style-type: none;
	padding: 0 30px;
	line-height: 20px;
	overflow: auto;
}
