.Item {
	display: flex;
	position: relative;
	overflow: hidden;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	color: #d3d3d3;
	cursor: pointer;
	user-select: none;
	aspect-ratio: 1/1;
	height: 75%;
	font-weight: 400;
}
.Item svg {
	width: 38px;
	aspect-ratio: 1/1;
	stroke: #24242d;
	stroke-linejoin: 'bevel';
}
.Item p {
	margin-top: -1px;
	font-size: 11px;
}

.Item.active {
	color: #c71585;
	font-weight: 600;
	stroke-width: 0;
}
