.Navbar {
	display: flex;
	position: absolute;
	justify-content: space-evenly;
	align-items: center;
	width: 100vw;
	height: 80px;
	bottom: 0;
	background-color: #24242d;
	padding: 0 2%;
	z-index: 6;
}

.Navbar .Item:nth-child(1) svg {
	stroke-width: 1;
}
.Navbar .Item:nth-child(2) svg {
	stroke-width: 1;
}
.Navbar .Item:nth-child(3) svg {
	stroke-width: 0.5;
}

