*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	-webkit-tap-highlight-color: transparent;
}

a {
	text-decoration: none;
	color: inherit;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

ul,
ol {
	list-style: none;
}

button,
input,
textarea,
select {
	font: inherit;
	background: none;
	border: none;
	outline: none;
}

img {
	max-width: 100%;
	height: auto;
	display: block;
}

p {
	line-height: 1.5;
}

form {
	margin: 0;
	padding: 0;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

body {
	display: flex;
	flex-direction: column;
	position: fixed;
	background-color: #171722;
	color: #d3d3d3;
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	height: 100%;
	overflow: hidden;
}

body button {
	border-radius: 8px;
}
