.Pages {
	position: relative;
	height: calc(100vh - 80px);
	/* display: grid;
	grid-template-columns: 100%; */
	display: flex;
	flex-direction: row;
	transition: transform 0.5s ease-in-out;
	width: 300vw;
	overflow: hidden;
}

.Page {
	/* grid-column: 1/2; */
	padding: 20px 30px;
	display: flex;
	flex: 1;
	width: 100vw;
	flex-shrink: 0;
	position: relative;
	flex-direction: column;
	scroll-snap-align: start;
	scroll-snap-stop: always;
}

.Pages.active-0 {
	transform: translateX(0%);
}
.Pages.active-1 {
	transform: translateX(-100vw);
}
.Pages.active-2 {
	transform: translateX(-200vw);
}

.ripple {
	position: absolute;
	border-radius: 50%;
	background: rgba(255, 255, 255, 0.1);
	transform: scale(0);
	animation: ripple-animation 0.6s ease-out;
	pointer-events: none;
}

@keyframes ripple-animation {
	to {
		transform: scale(2);
		opacity: 0;
	}
}
