.Card li {
	display: flex;
	flex-direction: column;
	position: relative;
	width: 100%;
	height: 60vw;
	border: solid #d3d3d3 1px;
	border-radius: 8px;
	background-color: #24242d;
	margin-bottom: 20px;
}
.Card li h6 {
	position: absolute;
	text-align: center;
	font-weight: 300;
	font-size: 12px;
	width: 100%;
	right: 0;
	left: 0;
	bottom: 4px;
}
.Card li img.preview {
	width: 100%;
	height: 40%;
	border-radius: 8px 8px 0 0;
	border: none;
	background-color: #555555;
}
.Card li .info {
	height: 50%;
	display: flex;
	flex-direction: column;
	padding: 10px;
	gap: 10%;
}
.Card li .info h3 {
	font-size: 6.4vw;
	font-weight: 500;
}
.Card li .info h4 {
	font-size: 5vw;
	font-weight: 300;
	line-height: 1.3em;
}
.Card li .info .downloads {
	display: flex;
	position: absolute;
	bottom: 4px;
	right: 7px;
	gap: 4px;
}
.Card li .info .downloads h5 {
	font-weight: 650;
	font-size: 12px;
	color: #32cd32;
}
.Card li .info .downloads svg {
	width: 13px;
	aspect-ratio: 1/1;
	color: #32cd32;
}
